<template>
  <b-container fluid class="mt--7">
    <b-row>
      <b-col lg="2" style="padding: 0;">
        <form-loader height_custom="260px" v-if="loading" />

        <card v-else header-classes="bg-transparent">
          <b-row class="group-filter">
            <b-col cols="6" lg="6" style="display: flex;">
              <base-button @click="initFilters" size="sm" class="filter" type="default">{{ $t('general.filter') }}
              </base-button>
              <base-button @click="eraseFilters" size="sm" class="filter" type="default">{{ $t('general.clear') }}
              </base-button>
            </b-col>
          </b-row>
          <br><br>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input input-classes="form-control-sm" type="text" :label="$t('cruds.products.index.number')"
                v-model="search_by_number" />
            </b-col>
          </b-row>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input input-classes="form-control-sm" type="text" :label="$t('cruds.products.index.name')"
                v-model="search_by_name" />
            </b-col>
          </b-row>

          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input :name="$t('cruds.products.index.type')" :label="$t('cruds.products.index.type')">
                <el-select v-model="search_by_type" :placeholder="$t('cruds.products.index.type')">
                  <el-option v-for="option in lists.types" :key="option.label"
                    :label="$t('cruds.products.category.' + option.label)" :value="option.value">
                  </el-option>
                </el-select>
              </base-input>
            </b-col>
          </b-row>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input :name="$t('cruds.products.index.category_id')" :label="$t('cruds.products.index.category_id')">
                <el-select v-model="search_by_category_id" :placeholder="$t('cruds.products.index.category_id')">
                  <el-option v-for="option in lists.categories" :key="option.label" :label="option.label"
                    :value="option.value">
                  </el-option>
                </el-select>
              </base-input>
            </b-col>
          </b-row>
        </card>
      </b-col>

      <b-col lg="10">
        <form-loader height_custom="410px" v-show="loading" />

        <card v-show="!loading">
          <b-col cols="12" lg="12" class="py-2 text-right" v-if="$can('product_create')">
            <base-button size="sm" class="btn btn-default" :title="$t('general.csv_export')">
              <i class="fa-solid fa-download"></i>
            </base-button>
            <base-button size="sm" class="btn btn-default" :title="$t('general.csv_import')">
              <i class="fa-solid fa-upload"></i>
            </base-button>
            <base-button size="sm" class="btn btn-default" :title="$t('general.print')">
              <i class="fa-solid fa-print"></i>
            </base-button>
            <base-button @click="navigateToList" size="sm" class="btn btn-default" :title="$t('general.list')">
              <i class="fa-solid fa-table-list"></i>
            </base-button>
            <base-button @click="navigateToCreate" size="sm" class="btn btn-default">
              {{ $t('general.new') }}
            </base-button>
          </b-col>

          <div class="row">
            <div v-for="item in data" :key="item.id" class="col-lg-3 col-md-6">
              <b-card class="card-content">
                <div>
                  <p class="mb-1" style="font-size: 10px;font-weight: bold;">
                    N <b>{{ item.id }}</b> - <b>{{ item.name }}</b>
                  </p>

                  <img alt="Image placeholder" class="card-image" :src="`http://192.168.100.51:8000${item.path}`" />

                  <b-row align-v="center" class="my-0 p-0">
                    <b-col sm="6">
                      <div>
                        <a class="card-action" @click="navigateToEdit(item)">
                          <i class="material-icons">edit</i>
                        </a>
                        <a class="card-action" @click="handleDelete(item)">
                          <i class="material-icons red-text" style="color: red;">delete</i>
                        </a>
                      </div>
                    </b-col>

                    <b-col sm="6">
                      <div>
                        <p class="mb-0" style="font-size: 10px;font-weight: bold;">
                          <b>{{ maskedValue(item.price) }}</b>
                        </p>
                      </div>
                    </b-col>
                  </b-row>
                </div>
              </b-card>
            </div>
          </div>

          <div class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap py-1">
            <p class="card-category">
              {{ total }} {{ $t('general.datatable_registers') }}
            </p>
            <base-pagination class="pagination-no-border py-0" :current="query.page" :per-page="query.limit"
              :total="total" @change="clickPagination($event)">
            </base-pagination>
          </div>
        </card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import products_filter from './mixins/products_filter'

export default {
  mixins: [products_filter],
  data() {
    return {
      xprops: {
        module: "ProductsIndex",
        route: "products",
        permission_prefix: "product_",
        view_action: {
          show: false,
          edit: true,
          delete: true,
        },
      },
    };
  },
  mounted() {
    this.query.limit = 8
    this.initFilters(this.query);
  },
  computed: {
    ...mapGetters("ProductsIndex", ["data", "total", "lists", "loading"]),
  },
  methods: {
    ...mapActions("ProductsIndex", [
      "fetchIndexData",
      "setQuery",
      "resetState",
      "setLinearFilters",
      "fetchCategoriesByTypeData",
      "destroyData"
    ]),
    navigateToCreate() {
      this.$router.push({ name: "products.create" });
    },
    navigateToList() {
      this.$setViewPath('products', '/')
      this.$router.push({ name: "products.index" });
    },
    navigateToEdit(row) {
      this.$router.push({ path: `/products/${row.id}/edit` });
    },
    maskedValue(value) {
      return this.$maskedValue(value, { language: 'contry', currency: 'BRL' })
    },
    handleDelete(row) {
      this.$swal.fire({
        title: 'Tem certeza?',
        text: `Ateno, voc no poder desfazer isto`,
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: 'Excluir',
        cancelButtonText: 'Cancelar',
        buttonsStyling: false
      }).then(result => {
        if (result.value) {
          this.destroyData(row.id)
          this.$swal.fire({
            title: 'Excluido!',
            text: `Voc removeu ${row.name}`,
            type: 'success',
            confirmButtonClass: 'btn btn-success btn-fill',
            buttonsStyling: false
          });
        }
      });
    }
  },
};
</script>

<style scoped>
.filter {
  font-size: xx-small;
}

.group-filter {
  margin-left: -26px;
  margin-bottom: -20px;
}

.card-image {
  width: 129px;
  height: 80px;
}

.card-content .card-body {
  padding-left: 12px;
  padding-top: 1px;
  padding-bottom: 2px;
}

.card-action {
  cursor: pointer;
}
</style>