<template>
  <b-container fluid class="mt--6">
    <card>
      <b-row align-v="center" slot="header">
        <b-col cols="8">
          <h3 class="mb-0">{{ $t('cruds.products.edit.title1') }}</h3>
        </b-col>
      </b-row>

      <validation-observer ref="formValidator">
        <b-form role="form">
          <div class="pl-lg-4">
            <b-row>
              <b-col lg="6">
                <h6 class="heading-small text-muted mb-4">{{ $t('cruds.products.create.basic_informations') }}</h6>
                <b-row>
                  <b-col lg="8">
                    <base-input class="mb-3" :placeholder="$t('cruds.products.index.name')"
                      :name="$t('cruds.products.index.name')" :label="$t('cruds.products.index.name')"
                      :rules="{ required: true }" :value="entry.name" @input="updateName" />
                  </b-col>
                  <b-col lg="4">
                    <base-input class="mb-3" :placeholder="$t('cruds.products.index.price')"
                      :name="$t('cruds.products.index.price')" :label="$t('cruds.products.index.price')"
                      :rules="{ required: true }" :value="entry.price" :mask="maskOpt" @input="updatePrice" />
                  </b-col>
                </b-row>
                <b-row>
                  <b-col lg="12">
                  <base-input class="mb-3" :placeholder="$t('cruds.products.index.description')"
                      :name="$t('cruds.products.index.description')" :label="$t('cruds.products.index.description')"
                      :rules="{ required: true }" :value="entry.description" @input="updateDescription" />
                  </b-col>
                </b-row>
                <b-row>
                  <b-col lg="6">
                    <base-input :name="$t('cruds.products.index.type')" :label="$t('cruds.products.index.type')"
                      :rules="{ required: true }">
                      <el-select :value="entry.type" :placeholder="$t('cruds.products.index.type')" @input="updateType">
                        <el-option v-for="option in lists.types" :key="option.label"
                          :label="$t('cruds.products.category.' + option.label)" :value="option.value">
                        </el-option>
                      </el-select>
                    </base-input>
                  </b-col>
                  <b-col lg="6">
                    <base-input :name="$t('cruds.products.index.category_id')"
                      :label="$t('cruds.products.index.category_id')" :rules="{ required: true }">
                      <el-select :value="entry.category_id" :disabled="enable_categories"
                        :placeholder="$t('cruds.products.index.category_id')" @input="updateCategoryId">
                        <el-option v-for="option in lists.categories" :key="option.label" :label="option.label"
                          :value="option.value">
                        </el-option>
                      </el-select>
                    </base-input>
                  </b-col>
                </b-row>
              </b-col>
              <b-col lg="6">
                <b-row>
                  <h6 class="heading-small text-muted mb-4">{{ $t('cruds.products.create.title2') }}</h6>
                </b-row>
                <b-row>
                  <img height="220px" style="max-width: 600px;" :src="background || `http://192.168.100.51:8000${entry.path}`"
                    alt="">
                </b-row>
                <br>
                <b-row>
                  <label>
                    <input @input="updateImage" accept="image/*" type="file" id="background-input" />
                  </label>
                </b-row>
              </b-col>
              <div class="text-center">
                <b-button type="submit" @click.prevent="indexSubmit" variant="primary" class="mt-4">
                  {{ $t('general.save') }}
                </b-button>
              </div>
            </b-row>
          </div>
        </b-form>
      </validation-observer>
    </card>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      maskOpt: "'alias': 'numeric', 'groupSeparator': '.', 'autoGroup': true, 'digits': 2, 'radixPoint': ',', 'digitsOptional': false, 'allowMinus': false, 'prefix': 'R$ ', 'placeholder': '0'",
      background: "",
      selectType: "",
      selectCategoryId: "",
      enable_categories: true
    };
  },
  computed: {
    ...mapGetters("ProductsSingle", ["entry", "loading", "lists"]),
  },
  watch: {
    "this.$route.params.id": {
      immediate: true,
      handler() {
        this.resetState();
        this.fetchEditData(this.$route.params.id);
      },
    },
  },
  beforeDestroy() {
    this.resetState();
  },
  methods: {
    ...mapActions("ProductsSingle", [
      "updateData",
      "fetchEditData",
      "fetchCategoriesByTypeData",
      "resetState",
      "setName",
      "setType",
      "setPrice",
      "setImage",
      "setDescription",
      "setCategoryId"
    ]),
    updateName(value) {
      this.setName(value);
    },
    updateDescription(value) {
      this.setDescription(value);
    },
    updateType(value) {
      this.setCategoryId([]);
      this.enable_categories = false
      this.fetchCategoriesByTypeData(value)
      this.setType(value);
    },
    updatePrice(value) {
      this.setPrice(value);
    },
    updateCategoryId(value) {
      this.setCategoryId(value);
    },
    updateImage(e) {
      let image = e.target.files[0];
      this.background = `${URL.createObjectURL(image)}`;
      this.setImage(image)
    },
    indexSubmit() {
      this.$refs.formValidator.validate().then((valid) => {
        if (valid) {
          this.updateData()
            .then(() => {
              this.$eventHub.$emit("update-success");
              this.$router.push({ path: this.$getViewPath('products') });
            })
            .catch((error) => {
              this.status = "failed";
              _.delay(() => {
                this.status = "";
              }, 3000);
            });
        }
      });
    }
  },
};
</script>
